@font-face {
    font-family: 'Poppins';
    src: url('../fonts/Poppins-Bold.ttf');
}

@font-face {
    font-family: 'PoppinsRegular';
    src: url('../fonts/Poppins-Regular.ttf');
}

@font-face {
    font-family: 'BeVietnamRegular';
    src: url('../fonts/BeVietnamPro-Regular.ttf');
}


html {
    scroll-behavior: smooth;
    
    ::-webkit-scrollbar{
        background-color: #05020a;
        width: 10px;
    }

    ::-webkit-scrollbar-thumb{
        background-color: #652572;
        border-radius: 4px;
    }
}

* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: 100vh;
    background-color: #000000;
    scroll-behavior: smooth;
    max-width: 1400px;
    margin: auto auto;
}

header {
    width: 100%;
    height: auto;
    background: linear-gradient(180deg, #18062B 0%, #18062B 53.13%);
    padding-bottom: 100px;
    position: relative;
}

.fmobile {

}

.headerImg {
    width: 100%;
    position: absolute;
    top: 80px;
    z-index: 3;
    pointer-events: none;
}

.headerSvg {
    position: absolute;
    top: 190px;
    right: 10px;
    opacity: 0.8;
    pointer-events: none;


}

.headerSvg2 {
    position: absolute;
    top: -60px;
    right: 100px;
    width: 400px;
    height: 400px;
    opacity: 0.5;
    pointer-events: none;

}

.svgs {
    width: 100%;
    height: 100px;
    display: flex;
    flex-flow: wrap;
    align-items: center;
    justify-content: center;
    position: absolute;
    top: 900px;
}

.svg {
    width: 1200px;
    height: 300px;
    margin-left: -100px;
    pointer-events: none;

}

.svg:last-child{
    margin-left: -300px;
    opacity: 0.5;

}

nav {
    width: 100%;
    height: 90px;
    padding: 0px 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    transition: .5s;
}

nav .title {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: #FFF;
    font-size: 30px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    transition: .5s;
}

nav .title span {
    color: #642470;
}

.menu {
    width: 40px;
    height: 40px;
    align-items: flex-start;
    justify-content: center;
    flex-direction: column;
    gap: 5px;
    display: none;
}

.menu span {
    background-color: #FFF;
    height: 3px;
    border-radius: 2px;
}

.menu span:nth-child(2) {
    width: 50%;
}

.menu span:nth-child(1) {
    width: 30%;
}

.menu span:nth-child(3) {
    width: 70%;
}

.signContainer button {
    transition: width .5s height .5s;
    width: 126px;
    height: 45px;
    flex-shrink: 0;
    background-color: transparent;
    border-radius: 10px;
    color: #D7D7D7;
    color: #BCBCBC;
    font-size: 12px;
    font-family: Poppins;
    font-style: normal;
    font-weight: 200;
    line-height: 22px;
    letter-spacing: 1.5px;
    text-transform: uppercase;
}

.signContainer .signUp {

    border: 1px solid #D7D7D7;
    transition: .3s;
}

.signContainer .signUp:hover{
    border-color: #642470;
    color: #642470;
}


.signContainer .signIn:hover{
    color: #642470;
}

.signContainer .signIn {
    border: none;
    margin-left: 10px;
    transition: .3s;
}

.intro {
    width: 100%;
    height: auto;
    display: flex;
    align-items: center;
    flex-flow: wrap;
    transition: .5s;
    justify-content: space-between;
    padding: 90px 80px 0px 80px;
}

.about {
    width: 50%;
    height: 100%;
}

.about h1 {
    color: #FFF;
    font-size: 52px;
    font-family: Poppins;
    font-style: normal;
    font-weight: 700;
    line-height: 82px;
    letter-spacing: -2px;
}

.about p {
    color: #E2E2E2;
    font-size: 14px;
    font-family: PoppinsRegular;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.5px;
}

.about button {
    display: flex;
    width: 218px;
    padding: 12px 24px;
    justify-content: center;
    align-items: center;
    gap: 10px;
    color: #652572;
    border-radius: 16px;
    background: #FFF;
    font-size: 12px;
    font-family: PoppinsRegular;
    font-style: normal;
    font-weight: 500;
    border: none;
    margin-top: 30px;
    line-height: normal;
    transition: .3s;
}

.about button:hover {
    background-color: #642470;
    color: #FFF
}

.aboutImg img {
    width: 400px;
    pointer-events: none;

}

.why {
    width: 100%;
    text-align: center;
    margin-top: 150px;
    color: #FFF;
    font-size: 28px;
    font-family: PoppinsRegular;
    font-style: normal;
    font-weight: 400;
    line-height: 72px;
    letter-spacing: -1px;
}

.services {
    width: 100%;
    height: auto;
    padding: 0px 80px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    margin-top: 50px;


    background: linear-gradient(180deg, #18062B 0%, #18062B 53.13% );
}

.service:first-child {
    background: rgba(255, 255, 255, 0.09);
    box-shadow: 2.009999990463257px -2.009999990463257px 20px 0px rgba(214, 214, 214, 0.17) inset;

}

.services .service {
    padding: 21px 17px;
    border-radius: 26px;
    width: 235px;
    height: 321px;
    flex-shrink: 0;
}

.service .iconContainer {
    width: 120px;
    height: 118px;
    flex-shrink: 0;
    border-radius: 26px;
    background: rgba(255, 255, 255, 0.09);
    box-shadow: -3.013333320617676px 3.013333320617676px 3.013333320617676px 0px rgba(255, 255, 255, 0.39) inset, 2.009999990463257px -2.009999990463257px 20px 0px rgba(214, 214, 214, 0.17) inset;
    backdrop-filter: blur(37.21466827392578px);
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 20px;
}

.service h3 {
    color: #FFF;
    font-size: 18px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-style: normal;
    font-weight: 600;
    line-height: 30px;
    letter-spacing: -0.408px;
}

.service p {
    color: #FFF;
    font-size: 14px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-style: normal;
    font-weight: 200;
    line-height: 18px;
}

.download {
    width: 100%;
    padding: 100px 100px 40px 80px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: linear-gradient(180deg, #000000 0%, #000000 95.13%);
}


.why.devs {
    background: linear-gradient(180deg, #000000 0%, #05020a 95.13%);
    margin-top: 0;
    margin-bottom: 0;
    padding: 130px 0px 40px 0px;

}

.download .view img{
    width: 430px;
    pointer-events: none;

}

.downInfo h1 {
    color: #FFF;
    font-size: 42px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-style: normal;
    font-weight: 700;
    line-height: 82px;
    letter-spacing: -2px;
}

.downInfo p {
    color: #E2E2E2;
    font-size: 14px;
    font-family: PoppinsRegular;
    font-style: normal;
    font-weight: 400;
    line-height: 24px;
    letter-spacing: 0.5px;
}

.downInfo p a {
    text-decoration: none;
}

.downOptions {
    margin-top: 30px;
}

.downOptions .option {
    width: 170px;
    height: 60px;
    background-color: transparent;
    border: none;
    cursor: pointer;
}

.downOptions .option:first-child {
    margin-right: 10px;
}

.downOptions .option img {
    width: 100%;
    pointer-events: none;

}

.view2 {
    display: none;
}

.devs {
  background: linear-gradient(180deg, #05020a 0%, #000 95.13%);
    display: flex;
    align-items: center;
    justify-content: center;
    gap: 40px;
    margin-bottom: 100px;
    position: relative;
}

.overImg {
    position: absolute;
    width: 100%;
    bottom: -1200px;
    pointer-events: none;

}

.dev {
    width: 298px;
}

.dev img {
    width: 298px;
    height: 313px;
    flex-shrink: 0;
    pointer-events: none;

}

.dev h3 {
    color: #FFF;
    font-size: 20px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-style: normal;
    font-weight: 600;
    line-height: 30px;
    letter-spacing: -0.408px;
    margin-top: 10px;
}

.dev .skills{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-top: 10px;
}

.dev .skills span {
    width: max-content;
    padding: 15px 9px 9px 9px;
    height: 30px;
    flex-shrink: 0;
    border-radius: 23px;
    border: 2px solid #642470;
    background: #652572;
    color: #FFF;
    text-align: center;
    padding-top: 5px;
    font-size: 11px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-style: normal;
    font-weight: 400;
    text-transform: uppercase;
}

.hr {
    width: 100%;
    height: 1px;
    background-color: #b8b8b89d;
    margin-top: 10px;
    margin-bottom: 10px;
}

.medias {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
}

.medias button:not(:last-child) {
    width: 65px;
    height: 63px;
    flex-shrink: 0;
    border-radius: 10px;
    background: radial-gradient(50% 50.00% at 50% 50.00%, #652572 0%, rgba(101, 37, 114, 0.00) 100%);
    border: none;
}

.medias button:last-child {
    width: 102px;
    height: 29px;
    flex-shrink: 0;
    border-radius: 8px;
    border: 1px solid #E2E2E2;
    background-color: transparent;
    color: #E2E2E2;
    transition: .3s;
}

.medias button:last-child:hover {
    border-color: #642470;
    color: #642470
}

.medias button img {
    width: 28px;
    height: 28.304px;
    flex-shrink: 0;
    pointer-events: none;

}

.diferenciais {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-flow: wrap;
    gap: 80px;
}

.diferencial {
    width: 340px;
    height: 400px;
    border-top: 2px solid #642470;
    border-bottom: 2px solid #141A4C;    
    border-radius: 35.025px;
    background: linear-gradient(139deg, rgba(58, 129, 191, 0.08) 0%, rgba(65, 48, 90, 0.08) 100%);
    backdrop-filter: blur(20px);
    margin-top: 40px;
    margin-bottom: 60px;
    padding: 60px 40px 0px 40px;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    flex-direction: column;
    
}

.fmobile {
    display: none;
}

.diferencial h3 {
    color: #FFF;
    font-size: 26px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-style: normal;
    font-weight: 600;
    line-height: 30px;
    letter-spacing: -0.408px;
}

.diferencial p {
    color: #FFF;
    font-size: 14px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-style: normal;
    font-weight: 200;
    line-height: 18px;
    text-align: center;
}

.diferencial button {
    width: 200px;
    height: 40px;
    color: #642470;
    background-color: #FFF;
    border: none;
    border-radius: 10px;
    font-family: 'PoppinsRegular';
    font-size: 12px;
    transition: .3s;
}

.diferencial button:hover {
    background-color: #18062B;
    color: #E2E2E2;
}

.diferencial:last-child {
    transform: translateX(-80px) scale(1.1);
    box-shadow: 0px 0px 3px 0px #642470;
    
}

.diferencial:first-child {
    transform: translateX(80px);
    box-shadow: 0px 0px 3px 0px #642470;

}

.questions {
    width: 100%;
    padding: 40px 27% 60px 27%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    gap: 10px;
}

.question {
    width: 100%;
    height: max-content;
    
}

.qheader {
    width: 100%;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.qheader h3 {
    color: #FFF;
    font-size: 18px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-style: normal;
    font-weight: 600;
    line-height: 30px;
    letter-spacing: -0.408px;
}

.hamb {
    width: 40px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    cursor: pointer;
}

.hamb span {
    width: 35%;
    height: 2px;
    background-color: #E2E2E2;
    border-radius: 2px;
}

.hamb span:first-child {
    transform: rotate(90deg);
}

.hamb span:last-child {
    transform: translateY(-2px);
}

.qbody {
    width: 100%;
    height: 60px;
    color: #FFF;
    font-size: 14px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-style: normal;
    font-weight: 200;
    line-height: 18px;
    overflow: hidden;
    transition: height .5s;
}

.qdivider {
    width: 100%;
    height: 2px;
    background-color: #61AAF2;
}

footer {
    width: 100%;
    padding: 40px 80px 40px 80px;
}

.links {
    width: 100%;
    padding: 20px 0px;   
}

.links ul{
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 10px;
    list-style: none;
}

.separator {
    width: 100%;
    height: 2px;
    background-color: #D7D7D7;
    margin-bottom: 20px;
}

.fcontent {
    color: #D7D7D7;
    font-family: 'PoppinsRegular';
    font-style: normal;
    line-height: 30px;
    letter-spacing: -0.408px;
}

.fcontent ul {
    display: flex;
    gap: 30px;
    list-style: none;
    margin-top: 10px;
}

.fcontent ul li a {
    text-decoration: none;
    color: #D7D7D7;
}

.bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 30px 0px 0px 0px;
}

.bottom h1 {
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    color: #FFF;
    font-size: 30px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}

.bottom h1 span {
    color: #642470;
}

.toTop {
    width: 40px;
    height: 40px;
    background-color: #642470;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 5px;
    transform: translateY(70px);
    opacity: 0;
    pointer-events: none;
    position: fixed;
    bottom: 40px;
    right: 80px;
    
}

.toTop img {
    width: 25px;
}

@keyframes up {
    to {
        transform: translateY(0px);
        opacity: 1;
        pointer-events: initial;
    }
}

.bodyTerms {
    background: linear-gradient(180deg, #141A4C 0%, #05031C 57.81%, #080321 100%);
}

main {
    width: 100%;
    padding: 40px 25%;
    text-align: center;
    color: #fff;
    margin-top: 60px;
}

main h1 {
    text-align: center;
    font-family: 'Roboto';
    font-size: 38px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
    margin-bottom: 5px;
}

main .date {
    margin-bottom: 20px;
    text-align: center;
    font-family: Roboto;
    font-size: 16px;
    font-style: normal;
    font-weight: 400;
    line-height: normal;
}

main h2 {
    margin-bottom: 15px;
    text-align: center;
    font-family: Roboto;
    font-size: 26px;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}

main h3 {
    margin-bottom: 5px;
    color: #FFF;
    font-size: 20px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    font-style: normal;
    font-weight: 600;
    line-height: 30px;
    letter-spacing: -0.408px;
}

main .text, .contact {
    color: #FFF;
    text-align: center;
    color: #fffcfca2;
    font-family: 'BeVietnamRegular';
    font-size: 15px;
    font-style: normal;
    line-height: 28px;
    margin-bottom: 30px;
}

main .contact {
    display: flex;
    flex-direction: column;
}

@keyframes down {
    to {
        transform: translateY(70px);
        opacity: 0;
        pointer-events: none;
    }
}




@media (max-width: 1190px) {
    .download .view img {
        transition: .5s;
        width: 35vw;
    }

    .questions {
        width: 100%;
        padding: 40px 20% 60px 20%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        gap: 10px;
    }

    .downInfo {
        margin-left: 80px;
    }

    .download {
        padding: 100px 30px 40 30px;
    }

    .downOptions .option {
        width: 150px;
    }

    .devs {
        flex-flow: wrap;
    }
}

@media (max-width: 970px) {
    nav {
        padding: 0px 50px ;
    }

    main {

        padding: 40px 20%;
    }
    
    .intro { 
        padding: 90px 50px 0px 50px;
    }

    .services {
        padding: 0px 50px;
    }

    .services .service {
        padding: 21px 17px;
        border-radius: 26px;
        width: 200px;
        height: 321px;

    }

    footer {
        padding: 40px 50px;
    }

    .toTop {
        right: 50px;
    }

    .aboutImg img{
        width: 350px;
    }

    .download {
        padding: 100px 30px 40px 30px;
    }
}

@media (max-width: 900px) {
    .downOptions .option {
        width: 130px;
    }

    .download .view img {
        width: 40vw;
    }
}

@media (max-width: 860px) {
    nav {

        padding: 0px 30px ;
    }

    .questions {
        width: 100%;
        padding: 40px 12% 60px 12%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        gap: 10px;
    }

    .intro { 
        padding: 90px 30px 0px 30px;
    }

    .services {
        padding: 0px 30px;
    }

    .services .service {
        padding: 21px 17px;
        border-radius: 26px;
        width: 200px;
        height: 321px;
    }

    .devs .dev:last-child {
        display: none;
    }

    .services .service:last-child {
        display: none;
    }

    footer {
        padding: 40px 30px;
    }

    .toTop {
        right: 30px;
    }

    .aboutImg img{
        width: 300px;
    }

    .download {
        padding: 100px 30px 40px 30px;
    }


    

    .about h1 {
        font-size: 35px;
        letter-spacing: .3px;
        word-spacing: 2px;
    }

    .services {
        flex-flow: wrap;
    }

    .download .view img {
        width: 330px;
    }
    .downInfo h1 {
        font-size: 36px;
    }

    .downInfo {
        margin-left: 40px;
    }

    .downOptions {
        margin-top: 10px;
    }

}

@media (max-width: 771px) {

    main h1 {
        font-size: 26px;
    }

    main h2 {
        font-size: 22px;
    }

    .diferenciais {
        padding: 0px 20px;
    }

    .diferenciais .diferencial {
        transform: translateX(0px);
    }

    .diferenciais .diferencial:last-child {
        display: none;
    }
}

@media (max-width: 730px) {
    nav .title{
        font-size: 25px;
    }


    .signContainer button {
        width: 100px;
        height: 36px;
    }

    .download {
        width: 100%;
        padding: 70px 20px 0px 20px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        background: linear-gradient(180deg, #000000 0%, #000000 95.13%);
    }

    .downInfo {
        margin-bottom: 30px;
    }

    .downInfo h1 {
        font-size: 36px;
        text-align: center;
    }
    
    .downInfo p {
        text-align: center;
        line-height: 24px;
        margin-bottom: 20px;
        padding: 0px 30px;
    }

    .downOptions {
        display: flex;
        padding: 0px 20px;
        align-items: center;
        justify-content: center;
    }

    .downOptions .option {
        width: 150px;
    }

    .view {
        display: none;
    }

    .view2 {
        display: initial;
        width: 100%;
        padding: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    
    .view2 img {
        width: 300px;
    }
}

@media (max-width: 670px) {

    main {
        padding: 40px 17%;
    }
    

    .services .service:nth-child(3) {
        display: none;
    }

    .devs .dev:nth-child(2) {
        display: none;
    }

    .aboutImg {
        padding-top: 50px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .aboutImg img {
        width: 370px;
    }

    nav .title{
        font-size: 20px;
    }

    .intro {
        padding: 90px 20px 0px 20px;
    }

    .about {
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    .about h1 {
        font-size: 32px;
        letter-spacing: .3px;
        word-spacing: 2px;
    }

    .about p {
        text-align: center;
        padding: 0px 40px;
    }

    .download .downInfo{
        margin-left: 0px;
    }
}

@media (max-width: 570px) {

    main {
        width: 100%;
        padding: 120px 13% 0px 13%;
        text-align: center;
        color: #fff;
        margin-top: 0px;
    }
    
    main h1 {
        text-align: center;
        font-family: 'Roboto';
        font-size: 38px;
        font-style: normal;
        font-weight: 700;
        line-height: normal;
        margin-bottom: 5px;
    }
    
    nav {
        background-color: #18062B;
        backdrop-filter: blur(0px);
        position: fixed;
        top: 0px;
        z-index: 30;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        padding: 10px 30px;
        /* height: 60px; */
        height: max-content;
        transition: height .3s;
        z-index: 31;
    }
    
    .top {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
    }

    .signContainer {
       height: 0px;
       width: 100%;
       display: flex;
       align-items: center;
       justify-content: flex-end;
       flex-direction: column;
       gap: 10px;
       transition: .5s;
       overflow: hidden;
    }

    .signContainer button:last-child {
        width: 100%;
        background-color: #ffffff;
        margin: 0px;
    }

    .signContainer button:last-child a {
        color: #18062B;
        text-decoration: none;
    }

    .signContainer button:first-child a {
        color: #ffffff;
        text-decoration: none;
    }

    .signContainer button:first-child {
        width: 100%;
    }

    .menu {
        display: flex;
        z-index: 32;
    }

    .why {
        margin-top: 100px;
        padding: 0px 20px;
        font-size: 24px;  
        line-height: 50px; 
    }

    .devs {
        overflow-x: hidden;
        padding: 0px 20px;
    }

    .diferenciais {
        padding: 0px 20px;
    }

    .diferenciais .diferencial {
        transform: translateX(0px);
    }

    .diferenciais .diferencial:last-child {
        display: none;
    }

    .questions {
        padding: 0px 20px;
    }

    footer {
        display: none;
    }
    
    .toTop {
        right: 20px;
        z-index: 50;
        overflow: hidden;
    }

    body {
        position: relative;
        height: 100vh;
    }

    .fmobile {
        display: initial;
        padding: 0px 20px;
        width: 100%;
    }

    .fmobile .social {
        margin-top: 160px;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }

    .fmobile .social img {
        width: 30%;
    }
    .fmobile .fundo {
        margin-top: 30px;
        text-align: center;
        color: #dadada;
        font-family: 'BeVietnamRegular';
        font-size: 14px;
        font-style: normal;
        line-height: 28px;
    }

    .headerImg {
        height: 600px;

        width: 100%;
    }

    .overImg {
        width: 100%;
        height: 1250px;
        right: 0px;
        top: 3300px;
        transform: rotate(180deg);
    }

}

@media (max-width: 470px) {
    .services .service:nth-child(2) {
        display: none;
    }
    .services .service {
        width: 285px;
    }
}

@media (max-width: 420px) {
    .intro .aboutImg img {
        width: 230px;
    }

    .about p {
        padding: 0px;
    }
    
    .downOptions .option {
        width: 120px;
    }

}

@media (max-width: 370px) {
    .view2 img {
        width: 260px;
    }
}

@media (max-width: 330px) {
    .devs .dev {
        width: max-content;
    }


    .devs .dev img {
        width: 243px;
        height: 262px;
    }

    .downOptions {
        padding: 0px;
    }

    .devs .dev img {
        width: 260px;
    }

    .devs .dev .skills span:first-child {
        display: none;
    }

    .devs .dev .medias button:not(:last-child) {
        display: none;
    }

    .devs .dev .medias button:last-child {
        width: 100%;
    }


    .diferenciais .diferencial {
        width: 240px;
    }

    .services .service {
        width: 250px;
    }

    .fmobile .fundo {
        font-size: 12px;
    }

    .about h1 {
        text-align: center;
        line-height: 40px;
    }

    
    .downInfo p {
        padding: 0px 10px;
    }
}

@media (max-width: 310px) {
    .downOptions {
        flex-direction: column;
    }

    .downOptions .option {
        width: 180px;
    }

    .downOptions .option img {
        width: 80%;
    }

    .devs .dev .skills span:nth-child(2) {
        display: none;
    }

    .services .service {
        width: 200px;
    }

    .devs .dev img {
        width: 220px;
    }

    .view2 img {
        width: 200px;
    }
}
